import React, { useEffect, useRef } from "react";
import Chart from "chart.js/auto";

const LineChart = ({ data, label }) => {
  const chartRef = useRef(null);
  let chartInstance = null;

  useEffect(() => {
    const ctx = chartRef.current.getContext("2d");

    // Initialize Chart.js with empty data
    chartInstance = new Chart(ctx, {
      type: "line",
      data: {
        labels: [],
        datasets: [
          {
            label: label,
            data: [],
            fill: true,
            backgroundColor: "rgba(178, 214, 63,0.3)", // Change the fill color
            borderColor: "rgb(178, 214, 63)", // Change the border color
            tension: 0.2,
          },
        ],
      },
    });

    // Function to update chart data
    const updateChartData = () => {
      const labels = data.labels.slice(0, chartInstance.data.labels.length + 1);
      const values = data.values.slice(0, chartInstance.data.labels.length + 1);

      // Update chart data
      chartInstance.data.labels = labels;
      chartInstance.data.datasets[0].data = values;
      chartInstance.update();

      // Check if all data points are rendered, if not, schedule another update
      if (labels.length < data.labels.length) {
        setTimeout(updateChartData, 100); // Adjust the delay as needed
      }
    };

    // Start updating chart data
    updateChartData();

    // Cleanup function
    return () => {
      if (chartInstance) {
        chartInstance.destroy();
      }
    };
  }, [data]);

  return <canvas ref={chartRef} />;
};

export default LineChart;
